.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__name {
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  padding-top: 24px;
  display: flex;
}

.card__price .price__title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price .price__num {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__stars {
  justify-content: space-between;
  display: flex;
}

.card__stars .star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  padding-left: 4px;
}

.card__stars--1 .star:nth-child(-n+1), .card__stars--2 .star:nth-child(-n+2), .card__stars--3 .star:nth-child(-n+3), .card__stars--4 .star:nth-child(-n+4), .card__stars--5 .star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

html {
  font-family: Roboto, Arial, sans-serif;
}

body {
  margin: 0;
}

* {
  box-sizing: border-box;
}

